const uuidv4 = require("uuid/v4");

const getAvarageRow = array => {
  const columnQty = array[0].cells.length;

  //   const avarageRow = [];
  //   for (let j = 0; j < columnQty; j++) {
  //     let columnTotal = 0;
  //     for (let i = 0; i < array.length; i++) {
  //       columnTotal += array[i].cells[j].amount;
  //     }
  //     avarageRow.push({
  //       id: uuidv4(),
  //       amount: (columnTotal / columnQty).toFixed(2)
  //     });
  //   }
  // return avarageRow;

  const avarageRow = array.reduce((acc, line) => {
    line.cells.forEach(
      (element, index) =>
        (acc[index] = acc[index] ? acc[index] + element.amount : element.amount)
    );
    return acc;
  }, []);

  return avarageRow.map(element => {
    return {
      id: uuidv4(),
      amount: (element / columnQty).toFixed(2)
    };
  });
};

export default getAvarageRow;
